import uniPopup from "@/components/uni-popup.vue"
var graceJS = require('../../common/grace.js');
var api = require('../../common/api.js')
var _self
export default {
	data() {
		return {
			host: api.host,
			master: {},
			show: {},
			avatar: '',
			stdate: '',
			enddate: '',
			stdatea: '',
			enddatea: '',
			answer: [],
			userInfo: {},
			uid: '',
			amount: 0,
			isshow: true,

		}
	},
	components: {
		uniPopup
	},
	onLoad() {
		_self = this;


		this.getdate();




	},

	onShow() {

	},
	methods: {
		oldvip(){
		  uni.navigateTo({
		      url: 'oldvip'
		  });	
		},
		
		exit() {
			_self.isshow = true;
			this.$refs.popup.close();
		},

		open() {
			this.$refs.popup.open();
			this.isshow = false;
		},


		getdate() {
			this.master = api.mainlist.master;
			this.show = api.mainlist.show;
			this.answer = api.mainlist.answershow;
			this.avatar = api.host + this.master.avatar;
			this.amount = api.mainlist.show.vip;

			let da = new Date();
			this.stdate = da.getFullYear() + '年' + (da.getMonth() + 1) + '月' + da.getDate() + '日';
			this.enddate = (da.getFullYear() + 1) + '年' + (da.getMonth() + 1) + '月' + da.getDate() + '日';
			this.stdatea = da.getFullYear() + '-' + (da.getMonth() + 1) + '-' + da.getDate();
			this.enddatea = (da.getFullYear() + 1) + '-' + (da.getMonth() + 1) + '-' + da.getDate();
		},

		addqzhi() {


			uni.login({
				success: (e) => {
					let orderid = api.RndNum(4);
					console.log(orderid);
					_self.saveorder(orderid);
					_self.isshow = false;

					uni.request({
						url: api.host +
							`/addons/epay/index/experience?code=${e.code}&amount=${_self.amount}&type=wechat&orderid=${orderid}`,
						success: (res) => {
							console.log(res);

							if (res.statusCode !== 200) {

								uni.showModal({
									content: "支付失败，请重试！",
									showCancel: false
								});

								return;
							}




							let paymentData = res.data;
							console.log("得到接口prepay_id", paymentData);
							uni.requestPayment({
								timeStamp: paymentData.timeStamp,
								nonceStr: paymentData.nonceStr,
								package: paymentData.package,
								signType: 'MD5',
								paySign: paymentData.paySign,
								success: (res) => {
									_self.saveuser(orderid);


								},
								fail: (res) => {
									//		uni.hideLoading();

									uni.showModal({
										content: "支付失败,原因为:订单被取消 ",
										showCancel: false
									})
								},
								complete: () => {
									_self.isshow = true;
									uni.hideLoading();
									//			this.loading = false;
								}
							})

						},
						fail: (e) => {
							//console.log("fail", e);
							//		this.loading = false;
							_self.isshow = true;
							uni.hideLoading();
							uni.showModal({
								content: "支付失败,原因为:订单被取消 ",
								showCancel: false
							})
						}
					})
				},
				fail: (e) => {
					console.log("fail", e);
					_self.isshow = true;
					//	this.loading = false;
					uni.hideLoading();
					uni.showModal({
						content: "支付失败,原因为:订单被取消 ",
						showCancel: false
					})
				}
			})
		},

		check() {
			wx.getSetting({
				success: function(res) {
					if (res.authSetting['scope.userInfo']) {

						// 已经授权，可以直接调用 getUserInfo 获取头像昵称
						wx.getUserInfo({
							success: function(res) {
								console.log(res, 'xxxxx');
								_self.userInfo = res.userInfo;
								_self.addqzhi();
								return false;
							}
						})
					} else {
						_self.isshow = false;
						_self.$refs.popup.open();


					}
				}
			});

		},

		getUserInfoFun: function(e) {
			console.log(e.detail.userInfo);

			if (e.detail.userInfo) {
				api.userInfo = e.detail.userInfo;
				this.check();

				//用户按了允许授权按钮
			} else {
				_self.isshow = true;
				this.$refs.popup.close();
				return false

			}
		},



		saveuser(orderid) {
			let ar = {};
			ar['wxname'] = this.userInfo['nickName'];
			ar['wximg'] = this.userInfo['avatarUrl'];
			ar['wxid'] = api.userid;
			ar['enddate'] = this.enddatea;
			ar['stdate'] = this.stdatea;
			ar['orderid'] = orderid;
			this.svuer(ar);

		},

		async svuer(arr) {


			let db = await api.GET('saveuser', arr);
			console.log(db);
			if (db.code == 1) {
				graceJS.navigate('/pages/home/index', 'reLaunch');
			} else {
				api.msg(db.msg);
				return false
			}
		},

		async saveorder(orderid) {
			let ar = {};
			ar['wxname'] = this.userInfo['nickName'];
			ar['wximg'] = this.userInfo['avatarUrl'];
			ar['wxid'] = api.userid;
			ar['orderid'] = orderid;
			ar['type'] = 0;


			let db = await api.GET('saveorder', ar);
			console.log(db, '完成');

		},





	},



}
